import { Component, OnInit, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {CarService} from "../car.service";
import { VERSION } from '@angular/material';

export interface DialogData {
  animal: string;
  name: string;
}

@Component({
  selector: 'app-pop1',
  templateUrl: './pop1.component.html',
  styleUrls: ['./pop1.component.scss']
})
export class Pop1Component implements OnInit {
  
  attandace:any[];
  attandanceTypes:any[];
  attandace_convert:any[];

  myDate = new Date();

  version = VERSION;
  constructor(
    public dialogRef: MatDialogRef<Pop1Component>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,private carService: CarService) {
      this.attandace = this.carService.getAttendace();
      this.attandanceTypes = this.carService.getAttendanceType();
      this.attandace_convert = alasql('SELECT a.stand_date, a.start_time, a.end_time ,a.is_late, b.name as annual \
                                         FROM ? a \
                                         LEFT OUTER JOIN ? b \
                                           ON a.annual = b.code',[this.attandace,this.attandanceTypes]);
    }

  onNoClick(): void {
    this.dialogRef.close();
  }

  ngOnInit() {
    console.log('1212');
  }

}
